*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  display: grid;
  place-content: center;
  height: 100vh;
  background-color: rgb(134, 134, 134);
}
.weatherApp{
  position: relative;
  width: 400px;
  height: 500px;
  border-radius: 10px;
  display: grid;
  place-content: center;
  box-shadow: 2px 2px 10px rgb(161, 161, 161), -2px -2px 10px rgb(160, 160, 160);
  color: white;
  background: rgb(0, 102, 255);
}
.mainInfo{
  text-align: center;
  box-shadow: 4px 4px 4px rgb(25, 60, 255),
  -4px -4px 4px rgb(32, 128, 255);
  border-radius: 10px;
  padding: 1.5rem;
  margin-top: 5rem;
}
.locationIcon i{
  font-size: 2.4rem;
  animation: moveLocation 2s ease-in-out infinite;

}
.location{
  font-size: 2.5rem;
  font-weight: 500;
}
.temp{
  font-size: 5rem;
  font-weight: 500;
}

.noData{
  font-size: 2.7rem;
}
.weatherConditionIcon{
position: absolute;
top:110px;
left:30px;

}
.icon{
  width: 70px;
  height: 70px;
  display: block;
}
.searchBar{
  position: absolute;
  top: 20px;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
}
.searchBar input{
  width: 90%;
  font-size: 1.8rem;
  padding: 0.8rem 2rem;
  background-color: transparent;
  backdrop-filter: blur(2px);
  color:white;
  border: none;
  box-shadow: 4px 4px 4px rgb(25, 60, 255),
  -4px -4px 4px rgb(32, 128, 255);
  border-radius: 5px;
  outline: none;
  border-radius: 10px;
  cursor: pointer;
}
.searchBar input::placeholder{
  color:white;
}
.dataNotFound{
  font-size: 3rem;
}
.bottomInfo{
  display: flex;
  padding: 1rem;
  margin-top: 0.5rem;
}
.temp_min,.temp_max,.feels_like{
  text-align: center;
  padding: 0.8rem;
  box-shadow: 2px 2px 2px rgb(25, 60, 255),
              -2px -2px 2px rgb(32, 128, 255);
  border-radius: 5px;
  margin: 5px;
}


@keyframes moveLocation{
  0%,100%{
    transform: translateY(0);
  }
  50%{
    transform: translateY(10px);
  }
}